@import 'assets/colors.scss';
@import 'assets/mixins.scss';

.auth-wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-form-container {
  min-width: 346px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.auth-form {
  padding: 70px;
  background: #ffffff;
  @include box-shadow();
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 624px;
  &__title {
    font-size: 48px;
    text-align: center;
    margin: 0;
    margin-bottom: 18px;
  }
  &__description {
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 16px;
    color: $description_auth;
    line-height: 28px;
    margin: 0;
    margin-bottom: 16px;
    :first-child {
      margin-right: 10px;
    }
  }
}
.additional-actions {
  margin-top: 40px;
  font-size: 14px;
}

.additional-actions + .additional-actions {
  margin-top: 16px;
}

@media only screen and (max-width: 768px) {
  .auth-wrapper {
    display: block;
    height: initial;
  }
  .auth-form {
    width: 100%;
    box-shadow: none;
    padding: 55px;
    &__title {
      font-size: 36px;
    }
  }
}
