@import 'assets/colors.scss';

div.user-auth-form {
  width: 100%;
  margin-bottom: 24px;
  .bp3-label {
    font-size: 14px;
    font-weight: 500;
    color: $main_grey;
  }
  .bp3-input {
    border: 1px solid $border_field;
    border-radius: 4px;
    box-shadow: none;
    height: 40px;
  }
  &.input-danger {
    .bp3-input {
      border-color: $additional_red;
    }
  }
}

.mb-16 {
  margin-bottom: 16px !important;
}
