/*
  Customize input placeholder
  Usage:
  @include placeholder {
    color: red;
  }
 */
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
}

@mixin centralize {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin ellipse-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin row-center {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

@mixin box-shadow {
  box-shadow: 4px 8px 30px rgba(51, 51, 53, 0.2);
}
